*, *::before, *::after {
    box-sizing: border-box;
  }
  .weather-card {
    margin-top: 5%;
    text-align: left;
    left: 50%;
    position: absolute;
    z-index: 1;
    width: 380px;
    margin-top: 7%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
  }
  .weather-card__content {
    padding: 2%;
    margin: 2%;
    background-color:rgba(255, 255, 255, 0.9);
    border-radius: 10px;
    height: 300px;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
    box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3), 0 17px 17px 0 rgba(0, 0, 0, 0.15);
  }
  #partly_cloudy {
    flex: 0 1 33%;
    margin-right: 2em;
    fill: #fff;
  }
  .weather-card__header {
    display: flex;
    align-items: center;
    justify-content: space-around;
    line-height: 1;
     -webkit-transform: rotate(0deg) scale(0.3,0.3) ;
             transform: rotate(0deg) scale(0.3,0.3) ; opacity: 0; 
       -webkit-transform: rotate(0deg) scale(1.05,1.05) ; 
               transform: rotate(0deg) scale(1.05,1.05) ; opacity: 1;  
       -webkit-transform: rotate(0deg) scale(0.9,0.9) ;  
               transform: rotate(0deg) scale(0.9,0.9) ;  
       -webkit-transform: rotate(0deg) scale(1.0,1.0) ;  
               transform: rotate(0deg) scale(1.0,1.0) ;  
   
  }
  
  .weather-card__temperature {
    display: block;
    font-size: 4em;
  }
  .weather-card__description {
    display: block;
    font-size: 1.5em;
  }
  .weather-card__forcast {
    width: 100%;
  }
  .weather-card__forcast > tr {
    padding: 15px;
  }
  .weather-card__forcast > tr > td {
    width: 50%;
  }
  .weather-card__navigation ul {
    display: flex;
    justify-content: space-around;
    list-style: none;
    margin: 2em 0 1em 0;
    padding: 0 0 1em 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.8);
  }

